}
.delete {
  background-color: dodgerblue;
  text-align: right;
  padding: 10px;
  color: white;
  display: none;
  transition: 0.5s;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 5px;
}
.delete button {
  cursor: pointer;
  background-color: transparent;
  border: none;
  color: white;
  z-index: 1;
}
.content:hover .delete {
